<template>
  <div class="home">

  <van-row>
    <h2 style="color:blue;">{{this.matrix}}</h2>
    <van-col span=24>
        <van-col span=11>
            <van-field v-model="input1" label="矩阵"  label-width="45px" :placeholder="exm" type="textarea" rows="4" style="white-space: pre-wrap;"/>
        </van-col>
        <van-col span=2>
            <span v-if="second" style="font-size:60px;">{{fuhao}}</span>
            <span v-if="mici" style="font-size:60px;">^</span>
        </van-col>
        <van-col span=11>
            <van-field v-if="second" v-model="input2" label="矩阵"  label-width="45px" :placeholder="exm" type="textarea" rows="4" style="white-space: pre-wrap;"/>

            <van-field v-if="mici" type="number" v-model="input2"   placeholder="请输入幂次"/>
			
			<van-field v-if="show34" type="number" v-model="input3" placeholder="请输入行"/>
			<van-field v-if="show34" type="number" v-model="input4" placeholder="请输入列"/>
        </van-col>
      </van-col>
  </van-row>
  <van-row>
    <van-col span=24>
        <div style="margin-top:20px;">
		    <h2 style="color:red;" v-if="success==false">{{message}}</h2>
            <van-field v-model="output" label-width="45px" label="结果" type="textarea" rows="4" style="white-space: pre-wrap;"/>
        </div>
        
        <van-radio-group v-model="radio" direction="horizontal" @change="onchange">
            <van-radio name="1"><h3>单矩阵运算</h3></van-radio>
            <van-radio name="2"><h3>两位运算</h3></van-radio>
        </van-radio-group>
        

        <div v-if="choise1">
            <van-cell v-for="(item,index) in list1" :key="index" clickable @click="upload1(index)">
                {{list1[index]}}
            </van-cell>
        </div>
        <div v-if="choise2">
            <van-cell v-for="(item,index) in list2" :key="index" clickable @click="upload2(index)">
                {{list2[index]}}
            </van-cell>
        </div>
    </van-col>

  </van-row>


  </div>
</template>

<script>
import { Toast } from 'vant';
import axios from 'axios';

export default {
  name: 'Matrix',
  data () {
    return {
      msg: '高等数学计算器',
	  matrix: '求行最简式',
      show:false,
      input1:'',
      input2:'',
	  input3:'',
	  input4:'',
	  show34:false,
      output:'',
      lastone:'',
      upload:'',
      lastout:'',
      mici:false,
      fuhao:'+',
      second:false,
      choise1:true,
      choise2:false,
      list1:[
	      '求解',
          '求行最简式',
          '求秩',
          '求转置矩阵',
          '求行列式',
          '求代数余子式',
          '求逆矩阵',
          '求伴随矩阵',
          '求矩阵的迹',
      ],
      list2:[
	      '求解',
          '+',
          '-',
          '*',
          '÷',
          '^'
      ],
      radio: '1',
      exm:'矩阵输入格式如下:\n1 2 3\n4 5 6\n7 8 9',
	  module:'1',
	  message:'',
	  success:true,
    }
  },
  methods:{
      clean(){
	      this.input1 = '';
		  this.input2 = '';
		  this.input3 = '';
		  this.input4 = '';
	  },
	  
      onchange(){
	      this.show34 = false;
		  this.success = true;
	      this.clean();
		  
          if(this.radio=='1'){
		      this.matrix = '求行最简式';
              this.choise1=true;
              this.choise2=false;
              this.second=false;
              this.mici=false;
          }
          else{
		      this.matrix = '计算矩阵相加';
              this.choise2=true;
              this.choise1=false;
              this.second=true;
              this.mici=false;
          }
      },
      upload2(way){//两位运算
	      this.show34 = false;
		  this.success = true;
		  
          if(way==1){//加法，传入input1和input2
		      this.matrix = "计算矩阵相加";
              this.fuhao='+';
              this.second=true;
              this.mici=false;
          }
          else if(way==2){//减法，传入input1和input2
		      this.matrix = '计算矩阵相减';
              this.fuhao='-';
              this.second=true;
              this.mici=false;
          }
          else if(way==3){//乘法，传入input1和input2
              this.matrix = '计算矩阵相乘';
			  this.fuhao='*';
              this.second=true;
              this.mici=false;
          }
          else if(way==4){//除法，传入input1和input2
              this.matrix = '计算矩阵相除';
			  this.fuhao='÷';
              this.second=true;
              this.mici=false;
          }
          else if(way==5){//幂次，传入input1和input2
              this.matrix = '计算矩阵的常数次幂';
			  this.second=false;
              this.mici=true;
          }
          else if(way==0){//上传并接收数据，输出在output
		      var _this = this;
			  axios.get('/calculatematrix',{
			      params:{
				      module:_this.fuhao,
					  input1:_this.input1,
					  input2:_this.input2,
				  }
			  })
			  .then(function(response){
			      _this.success = response.data.success;
				  _this.message = response.data.message;
			      _this.output = response.data.result;
			  })
			  .catch(function(error){
			      console.log(error);
			  });
          }
          else{}
      },
      upload1(way){//单矩阵运算
		  this.success = true;
		  
	      if(way == 0){
		      var _this = this;
		      axios.get('/calculatematrix',{
				  params:{
				      module:_this.module,
		              input1:_this.input1,
					  input3:_this.input3,
					  input4:_this.input4,
				  }
			  })
		      .then(function(response){
	              _this.success = response.data.success;
		          _this.message = response.data.message;
		          _this.output = response.data.result;
		      })
		      .catch(function(error){
		          console.log(error);
		      });
		  }
		  else{
			  this.input2 = '';
			  this.input3 = '';
			  this.input4 = '';
		      if(way == 5){
			      this.show34 = true;
			  }
			  else{
			      this.show34 = false;
			  }
		      this.module = way;
			  this.matrix = this.list1[way];
		  }
      },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
}
</style>



