<template>
  <div class="home">

	<van-row type="flex" justify="center">
    <van-col span=30>
        <van-col span=4>
            <van-field v-model="input1" clearable @click="in1" placeholder="系数" />
        </van-col>
        <van-col span=1 style="font-size:30px;">
            x⁴+
        </van-col>
        <van-col span=4>
            <van-field v-model="input2" clearable @click="in2" placeholder="系数" />
        </van-col>
        <van-col span=1 style="font-size:30px;">
            x³+
        </van-col>
        <van-col span=4>
            <van-field v-model="input3" clearable @click="in3" placeholder="系数" />
        </van-col>
        <van-col span=1 style="font-size:30px;">
            x²+
        </van-col>
        <van-col span=4>
            <van-field v-model="input4" clearable @click="in4" placeholder="系数" />
        </van-col>
        <van-col span=1 style="font-size:30px;">
            x+
        </van-col>
		    <van-col span=3>
				<van-field v-model="input5" clearable @click="in5" placeholder="常数" />
			</van-col>
		<van-col span=1 style="font-size:30px;">
            = 0
        </van-col>
		
    </van-col>
  </van-row>
  
  <h3 v-if="this.success==false">{{message}}</h3>
  
  <van-row style="margin-top:16px;">
      <span><van-button type="info" @click="upload">求解</van-button></span>
      <span><van-button type="info" @click="del">清空系数</van-button></span>
  </van-row>

  <van-row style="margin-top:16px;" type="flex" justify="center">
    <van-col span=20>
      <van-cell v-for="(item,index) in anslist" :key="item">
          <span style="font-size:20px;">{{x[index]}}</span>
          <span style="font-size:20px;">{{anslist[index]}}</span>
      </van-cell>
    </van-col>
	</van-row>

    <div>
        <van-popup
        v-model="show"
        closeable
        close-icon-position="top-right"
        position="bottom"
        :style="{ height: '30%' }"/>
    </div>


  </div>
</template>

<script>
import { Toast } from 'vant';
import axios from 'axios';
export default {
  name: 'Unary_multiple_equation',
  data () {
    return {
      msg: '高等数学计算器',
      show:false,
      input1:'0',
      input2:'0',
      input3:'0',
      input4:'0',
	  input5:'0',
      output:'',
      lastone:'',
      lastout:'',
	  message:'',
	  success:true,
      anslist:[
      ],
      x:[
          'x1=',
          'x2=',
          'x3=',
          'x4=',
      ]
    }
  },
  methods:{
      in1(){
          if(this.input1=='0'){
              this.input1='';
          }
          if(this.input2==''){
              this.input2='0';
          }
          if(this.input3==''){
              this.input3='0';
          }
          if(this.input4==''){
              this.input4='0';
          }
          if(this.input5==''){
              this.input5='0';
          }
      },
      in2(){
          if(this.input2=='0'){
              this.input2='';
          }
          if(this.input1==''){
              this.input1='0';
          }
          if(this.input3==''){
              this.input3='0';
          }
          if(this.input4==''){
              this.input4='0';
          }
          if(this.input5==''){
              this.input5='0';
          }
      },
      in3(){
          if(this.input3=='0'){
              this.input3='';
          }
          if(this.input2==''){
              this.input2='0';
          }
          if(this.input1==''){
              this.input1='0';
          }
          if(this.input4==''){
              this.input4='0';
          }
          if(this.input5==''){
              this.input5='0';
          }
      },
      in4(){
          if(this.input4=='0'){
              this.input4='';
          }
          if(this.input2==''){
              this.input2='0';
          }
          if(this.input3==''){
              this.input3='0';
          }
          if(this.input1==''){
              this.input1='0';
          }
          if(this.input5==''){
              this.input5='0';
          }
      },
      in5(){
          if(this.input5=='0'){
              this.input5='';
          }
          if(this.input2==''){
              this.input2='0';
          }
          if(this.input3==''){
              this.input3='0';
          }
          if(this.input4==''){
              this.input4='0';
          }
          if(this.input1==''){
              this.input1='0';
          }
      },
      upload(){
          var _this = this;
	      axios.get('/equation',{
		      params: {
			      input1:_this.input1,
				  input2:_this.input2,
				  input3:_this.input3,
				  input4:_this.input4,
				  input5:_this.input5,
			  }
          })
          .then(function (response){
		      console.log(response.data.success);
			  _this.success = response.data.success;
		      if(response.data.success==true){
			      _this.anslist = response.data.res;
			  }
			  else{
			      _this.message = response.data.message;
			  }
	      })
          .catch(function (error) { // 请求失败处理
              console.log(error);
          });
      },
      del(){
          this.input1='0';
          this.input2='0';
          this.input3='0';
          this.input4='0';
          this.input5='0';
      }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
</style>



