<template>
  <div>
    
    <div class="HelloWorld" ref="myechart" id="main"></div>
    <van-cell-group>
        <van-field v-model="input" label="函数F(x)=" placeholder="请输入需要绘制图像的函数" />
		<van-field v-model="left" type="number" label="区间下限" placeholder="请输入需要绘制图像的区间下限" />
		<van-field v-model="right" type="number" label="区间上限" placeholder="请输入需要绘制图像的区间上限" />
		<van-field v-model="dt" type="number" label="精度" placeholder="请输入图像精度" />
    </van-cell-group>
    <h2>F(x)={{input}}</h2>
	<p><h2 style="color:red;" v-if="this.success==false">{{message}}</h2></p>
	<van-button type="info" @click="draw">确定</van-button>
    
   </div>
</template>
<script>
import axios from 'axios';

  export default {
    name:'paint',
    data(){
      return{
        input:'',
		left:'1',
		right:'10',
		dt:'0.01',
		success:true,
		message:'',
        fdata:[],
		point:[
			{x:0.0,y:0.0}
		],
        f1:[1,2],
        f2:[3,4],
        f3:[5,6],
        // 定义图表，各种参数
        option:{  
          backgroundColor:"lightblue",
          grid:{
              containLabel:true,
          },
          xAxis:{
            name:'x',
            show:true,
            gridIndex:0,
            splitNumber:10,
            type:"value",
          },
          yAxis:{
            type:"value",
            name:'y',
            splitNumber:10,
          },
          series:[
            {
              data:[],
              type:"line",
              smooth:true,
            },
          ]
        }
      }
    },
    mounted:function(){
      console.log(this.$echarts)
      let myechart = this.$echarts.init(this.$refs.myechart) //初始化一个echarts
      myechart.setOption(this.option)  //setOption 用this.option中的数据开始作图
	  
    },
    
    methods:{
		initEcharts(){
			this.option.series[0].data = [[1,2],[2,5],[3,8]];
		},
        onsubmit(){
            this.$router.push({
                path:'/',
            })
        },
		draw(){
			var _this = this;
			axios.get('/drawfunction',{
				params:{
					input:_this.input,
					left:_this.left,
					right:_this.right,
					dt:_this.dt,
				}
			})
			.then(function(response){
				_this.success = response.data.success;
				if(response.data.success == true){
					var i=0;
					_this.fdata = [];
					for(i=0;i<response.data.point.length;i++){
						_this.point = response.data.point[i];
						_this.fdata.push([_this.point.x,_this.point.y]);
					}
					var myChart = _this.$echarts.init(document.getElementById('main'));
					myChart.setOption({
						series:[
							{
							data:_this.fdata,
							type:"line",
							smooth:true,
							},
						]
					});
				}
				else{
					_this.message = response.data.message;
				}
			})
			.catch(function(error){
				console.log(error);
			});
		}
    },
    created(){
        this.input=this.$route.query.fx;
    }
   
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.HelloWorld{   //注意画布必须定义大小
  width: 400px;
  height:600px;
  background: #cce6f0;
  margin: 0 auto;
}
</style>


