<template>
  <div class="home">

	<van-row>

    <van-col span=24>
        <van-field v-model="min" type="number" label="区间下限" placeholder="请从电脑键盘输入" />
        <van-field v-model="max" type="number" label="区间上限" placeholder="请从电脑键盘输入" />
        <van-field v-model="input" label="请输入函数" placeholder="请输入" />
        <van-field v-model="output" input-align="right"/>
        
		<h2 style="color:red;" v-if="this.success==false">{{message}}</h2>
		
		<van-grid clickable :column-num="4" :gutter="8" style="margin-top:8px;">
          <van-grid-item text="设置" @click="button1" />
          <van-grid-item text="x" @click="button2"/>
          <van-grid-item text="恢复式子" @click="button3" />
          <van-grid-item text="重启" @click="button4"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="7" @click="button5"/>
          <van-grid-item  text="8" @click="button6"/>
          <van-grid-item  text="9" @click="button7"/>
          <van-grid-item  text="DEL" @click="button8"/>
          <van-grid-item  text="AC" @click="button9"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="4" @click="button10"/>
          <van-grid-item  text="5" @click="button11"/>
          <van-grid-item  text="6" @click="button12"/>
          <van-grid-item  text="*" @click="button13"/>
          <van-grid-item  text="÷" @click="button14"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="1" @click="button15"/>
          <van-grid-item  text="2" @click="button16"/>
          <van-grid-item  text="3" @click="button17"/>
          <van-grid-item  text="+" @click="button18"/>
          <van-grid-item  text="-" @click="button19"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="0" @click="button20"/>
          <van-grid-item  text="." @click="button21"/>
          <van-grid-item  text="*10^n" @click="button22"/>
          <van-grid-item  text="Ans" @click="button23"/>
          <van-grid-item  :icon="require('../assets/qiujie1.jpg')" @click="button24"/>
      </van-grid>

      <van-grid clickable :column-num="6" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="(" @click="button25('(')"/>
          <van-grid-item  text=")" @click="button25(')')"/>
          <van-grid-item  text="cos(x)" @click="button25('cos(')"/>
          <van-grid-item  text="sin(x)" @click="button25('sin(')"/>
          <van-grid-item  text="tan(x)" @click="button25('tan(')"/>
          <van-grid-item  text="!" @click="button25('!')"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="arccos(x)" @click="button25('arccos(')"/>
          <van-grid-item  text="arcsin(x)" @click="button25('arcsin(')"/>
          <van-grid-item  text="arctan(x)" @click="button25('arctan(')"/>
          <van-grid-item  text="e" @click="button25('e')"/>
          <van-grid-item  text="π" @click="button25('π')"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="√" @click="button25('√(')"/>
          <van-grid-item  text="x^2" @click="button25('^2')"/>
          <van-grid-item  text="x^3" @click="button25('^3')"/>
          <van-grid-item  text="x^n" @click="button25('^')"/>
          <van-grid-item  text="log(x,n)" @click="button25('log(')"/>
      </van-grid>

      <van-grid clickable :column-num="6" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="lg(x)" @click="button25('lg(')"/>
          <van-grid-item  text="ln(x)" @click="button25('ln(')"/>
          <van-grid-item  text="," @click="button25(',')"/>
          <van-grid-item  text="x^n" @click="button25('^')"/>
          <van-grid-item  text="sec(x)" @click="button25('sec(')"/>
          <van-grid-item  text="3√()" @click="button25('3√(')"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="cot(x)" @click="button25('cot(')"/>
          <van-grid-item  text="csc(x)" @click="button25('csc(')"/>
          <van-grid-item  text="sinh(x)" @click="button25('sinh(')"/>
          <van-grid-item  text="cosh(x)" @click="button25('cosh(')"/>
          <van-grid-item  text="tanh(x)" @click="button25('tanh(')"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="sech(x)" @click="button25('sech(')"/>
          <van-grid-item  text="coth(x)" @click="button25('coth(')"/>
          <van-grid-item  text="csch(x)" @click="button25('csch(')"/>
          <van-grid-item  text="/" @click="button25('/')"/>
          <van-grid-item  text="°" @click="button25('°')"/>
      </van-grid>
    </van-col>

    <van-col span="12">
    </van-col>

	</van-row>

    <div>
        <van-popup
        v-model="show"
        closeable
        close-icon-position="top-right"
        position="bottom"
        :style="{ height: '30%' }"/>
    </div>


  </div>
</template>

<script>
import { Toast } from 'vant';
import axios from 'axios';

export default {
  name: 'DefiniteIntegral',
  data () {
    return {
      msg: '高等数学计算器',
      show:false,
      input:'',
      output:'0',
      lastone:'',
      lastout:'',
	  success:true,
	  message:'',
    }
  },
  methods:{
      button1(){//设置
         
      },
      button2(){//更多
          this.input+='x';
      },
      button3(){//恢复上一次按等号后的式子
          this.input=localStorage.getItem('f');
      },
      button4(){//重启
          this.input='';
          this.output='';
          this.lastone='';
      },
      button5(){
          this.input+='7';
          this.lastone='7';
      },
      button6(){
          this.input+='8';
          this.lastone='8';
      },
      button7(){
          this.input+='9';
          this.lastone='9';
      },
      button8(){//撤销
          if(this.input!=''){
              this.input=this.input.substr(0,this.input.length-1);
          }
      },
      button9(){//清空输入框
          this.input='';
      },
      button10(){
          this.input+='4';
          this.lastone='4';
      },
      button11(){
          this.input+='5';
          this.lastone='5';
      },
      button12(){
          this.input+='6';
          this.lastone='6';
      },
      button13(){
          this.input+='*';
          this.lastone='*';
          localStorage.setItem('f',this.input);
      },
      button14(){
          this.input+='÷';
          this.lastone='÷';
          localStorage.setItem('f',this.input);
      },
      button15(){
          this.input+='1';
          this.lastone='1';
      },
      button16(){
          this.input+='2';
          this.lastone='2';
      },
      button17(){
          this.input+='3';
          this.lastone='3';
      },
      button18(){
          this.input+='+';
          this.lastone='+';
          localStorage.setItem('f',this.input);
      },
      button19(){
          this.input+='-';
          this.lastone='-';
          localStorage.setItem('f',this.input);
      },
      button20(){
          this.input+='0';
          this.lastone='0';
      },
      button21(){
          this.input+='.';
          this.lastone='.';
      },
      button22(){
          this.input+='*10^';
      },
      button23(){//Ans
          this.input+=this.lastout;
          this.lastone=this.lastout;
      },
      button24(){//提交数据给后端(=)
          localStorage.setItem('f',this.input);
		  var _this = this;
		  axios.get('/integral',{
			params:{
				input: _this.input,
				left:_this.min,
				right:_this.max,
			}
		  })
		  .then(function(response){
			_this.success = response.data.success;
			if(response.data.success == true){
				_this.output = response.data.result;
			}
			else{
				_this.message = response.data.message;
			}
		  })
		  .catch(function(error){
			console.log(error);
		  });
      },
      button25(ch){
          this.input+=ch;
          this.lastone=ch;
          localStorage.setItem('f',this.input);
      },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
</style>



