<template>
  <div class="home">
    <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
    </div>

    <van-tabs @click="change">
        <van-tab title="科学计算器" ></van-tab>
        <van-tab title="函数图像" ></van-tab>
        <van-tab title="∑求和" ></van-tab>
        <van-tab title="定积分" ></van-tab>
        <van-tab title="求导" ></van-tab>
        <van-tab title="解线性方程组" ></van-tab>
        <van-tab title="一元多次方程" ></van-tab>
        <van-tab title="矩阵运算" ></van-tab>
        <van-tab title="复数运算" ></van-tab>
        <van-tab title="进制数计算" ></van-tab>
    </van-tabs>

    <h1>{{ msg }}</h1>

    <van-row>
        <div v-if="c9" style="margin:35px;">
            <paint></paint>
            <van-divider  :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
    </van-row>

    <van-row>
        <div v-if="c6" style="margin:35px;">
            <Unary_multiple_equation></Unary_multiple_equation>
            <van-divider :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
    </van-row>

    <van-row>
        <div v-if="c5" style="margin:35px;">
            <Matrix></Matrix>
            <van-divider :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
    </van-row>

    <van-row>
        <div v-if="c4" style="margin:35px;">
            <SolveEquation></SolveEquation>
            <van-divider :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
    </van-row>

    <van-row type="flex" justify="center">
	<van-col span="15">
        <div v-if="c1">
            <Summation></Summation>
            <van-divider :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
	</van-col>
    </van-row>
        
    <van-row type="flex" justify="center">
	<van-col span="15">
        <div v-if="c3">
            <Derivation></Derivation>
            <van-divider :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
	</van-col>
    </van-row>
        
    <van-row type="flex" justify="center">
	<van-col span="15">
        <div v-if="c2">
            <DefiniteIntegral></DefiniteIntegral>
            <van-divider :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
	</van-col>
    </van-row>

    <van-row type="flex" justify="center">
	<van-col span="15">
        <div v-if="c7">
            <pluralism></pluralism>
            <van-divider :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
	</van-col>
    </van-row>

    <van-row type="flex" justify="center">
	<van-col span="15">
        <div v-if="c8">
            <Number_system></Number_system>
            <van-divider :style="{ color: 'red', borderColor: 'red', padding: '0 16px' }"/>
        </div>
	</van-col>
    </van-row>



  <div style="margin-top:50px;">
	<van-row type="flex" justify="center">
    <van-col span=18 >

        <van-field v-model="input" type="textarea" rows="2" placeholder="请输入" />
        <van-field v-model="output" input-align="right"/>


        <van-grid clickable :column-num="4" :gutter="8" style="margin-top:8px;" border="true">
          <van-grid-item text="设置" @click="button1" />
          <van-grid-item text="更多计算器" @click="button2"/>
          <van-grid-item text="恢复式子" @click="button3" />
          <van-grid-item text="重启" @click="button4"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="7" @click="button5"/>
          <van-grid-item  text="8" @click="button6"/>
          <van-grid-item  text="9" @click="button7"/>
          <van-grid-item  text="DEL" @click="button8"/>
          <van-grid-item  text="AC" @click="button9"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="4" @click="button10"/>
          <van-grid-item  text="5" @click="button11"/>
          <van-grid-item  text="6" @click="button12"/>
          <van-grid-item  text="*" @click="button13"/>
          <van-grid-item  text="÷" @click="button14"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="1" @click="button15"/>
          <van-grid-item  text="2" @click="button16"/>
          <van-grid-item  text="3" @click="button17"/>
          <van-grid-item  text="+" @click="button18"/>
          <van-grid-item  text="-" @click="button19"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="0" @click="button20"/>
          <van-grid-item  text="." @click="button21"/>
          <van-grid-item  text="*10^n" @click="button22"/>
          <van-grid-item  text="Ans" @click="button23"/>
          <van-grid-item  :icon="require('../assets/equal.jpg')" @click="button24"/>
      </van-grid>

      <van-grid clickable :column-num="6" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="(" @click="button25('(')"/>
          <van-grid-item  text=")" @click="button25(')')"/>
          <van-grid-item  text="cos(x)" @click="button25('cos(')"/>
          <van-grid-item  text="sin(x)" @click="button25('sin(')"/>
          <van-grid-item  text="tan(x)" @click="button25('tan(')"/>
          <van-grid-item  text="!" @click="button25('!')"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="arccos(x)" @click="button25('arccos(')"/>
          <van-grid-item  text="arcsin(x)" @click="button25('arcsin(')"/>
          <van-grid-item  text="arctan(x)" @click="button25('arctan(')"/>
          <van-grid-item  text="e" @click="button25('e')"/>
          <van-grid-item  text="π" @click="button25('π')"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="√" @click="button25('√(')"/>
          <van-grid-item  text="x^2" @click="button25('^2')"/>
          <van-grid-item  text="x^3" @click="button25('^3')"/>
          <van-grid-item  text="x^n" @click="button25('^')"/>
          <van-grid-item  text="log(x,n)" @click="button25('log(')"/>
      </van-grid>

      <van-grid clickable :column-num="6" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="lg(x)" @click="button25('lg(')"/>
          <van-grid-item  text="ln(x)" @click="button25('ln(')"/>
          <van-grid-item  text="," @click="button25(',')"/>
          <van-grid-item  text="x^n" @click="button25('^')"/>
          <van-grid-item  text="sec(x)" @click="button25('sec(')"/>
          <van-grid-item  text="3√()" @click="button25('3√(')"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="cot(x)" @click="button25('cot(')"/>
          <van-grid-item  text="csc(x)" @click="button25('csc(')"/>
          <van-grid-item  text="sinh(x)" @click="button25('sinh(')"/>
          <van-grid-item  text="cosh(x)" @click="button25('cosh(')"/>
          <van-grid-item  text="tanh(x)" @click="button25('tanh(')"/>
      </van-grid>

      <van-grid clickable :column-num="5" :gutter="8"  style="margin-top:8px;">
          <van-grid-item  text="sech(x)" @click="button25('sech(')"/>
          <van-grid-item  text="coth(x)" @click="button25('coth(')"/>
          <van-grid-item  text="csch(x)" @click="button25('csch(')"/>
          <van-grid-item  text="/" @click="button25('/')"/>
          <van-grid-item  text="°" @click="button25('°')"/>
      </van-grid>
    </van-col>

	</van-row>
  </div>

  </div>
</template>

<script>
import Summation from './Summation';
import DefiniteIntegral from './DefiniteIntegral';
import Derivation from './Derivation';
import SolveEquation from './SolveEquation';
import Matrix from './Matrix';
import pluralism from './pluralism';
import Unary_multiple_equation from './Unary_multiple_equation'
import axios from 'axios';
import Number_system from './Number_system'
import paint from './paint'

export default {
  name: 'home',
  data () {
    return {
      msg: '高等数学计算器',
      show:false,
      imgSrc:require('../assets/background0.jpg'),
      input:'',
      output:'0',
      lastone:'',
      c1:false,
      c2:false,
      c3:false,
      c4:false,
      c5:false,
      c6:false,
      c7:false,
      c8:false,
      c9:false,
      lastout:'',
	    message: '',
      finished:false,
      list:[],
      variate:'',
    }
  },
  methods:{
      addx(){
          if(this.list.length<=9){
              this.list.push(this.variate);
              this.variate='';
          }
          else{
              this.finished=true;
          }
      },
      delatex(){
          this.list.pop();
      },
      change(name,title){
          if(title=='∑求和'){
              this.c1=true;
              this.c2=false;
              this.c3=false;
              this.c4=false;
              this.c5=false;
              this.c6=false;
              this.c7=false;
              this.c8=false;
              this.c9=false;
          }
          else if(title=='定积分'){
              this.c1=false;
              this.c2=true;
              this.c3=false;
              this.c4=false;
              this.c5=false;
              this.c6=false;
              this.c7=false;
              this.c8=false;
              this.c9=false;
          }
          else if(title=='求导'){
              this.c1=false;
              this.c2=false;
              this.c3=true;
              this.c4=false;
              this.c5=false;
              this.c6=false;
              this.c7=false;
              this.c8=false;
              this.c9=false;
          }
          else if(title=='解线性方程组'){
              this.c1=false;
              this.c2=false;
              this.c3=false;
              this.c4=true;
              this.c5=false;
              this.c6=false;
              this.c7=false;
              this.c8=false;
              this.c9=false;
          }
          else if(title=='矩阵运算'){
              this.c1=false;
              this.c2=false;
              this.c3=false;
              this.c4=false;
              this.c5=true;
              this.c6=false;
              this.c7=false;
              this.c8=false;
              this.c9=false;
          }
          else if(title=='一元多次方程'){
              this.c1=false;
              this.c2=false;
              this.c3=false;
              this.c4=false;
              this.c5=false;
              this.c6=true;
              this.c7=false;
              this.c8=false;
              this.c9=false;
          }
          else if(title=='复数运算'){
              this.c1=false;
              this.c2=false;
              this.c3=false;
              this.c4=false;
              this.c5=false;
              this.c6=false;
              this.c7=true;
              this.c8=false;
              this.c9=false;
          }
          else if(title=='进制数计算'){
              this.c1=false;
              this.c2=false;
              this.c3=false;
              this.c4=false;
              this.c5=false;
              this.c6=false;
              this.c7=false;
              this.c8=true;
              this.c9=false;
          }
          else if(title=='函数图像'){
              this.c1=false;
              this.c2=false;
              this.c3=false;
              this.c4=false;
              this.c5=false;
              this.c6=false;
              this.c7=false;
              this.c8=false;
              this.c9=true;
          }
          else{
              this.c1=false;
              this.c2=false;
              this.c3=false;
              this.c4=false;
              this.c5=false;
              this.c6=false;
              this.c7=false;
              this.c8=false;
              this.c9=false;
          }
      },



      button1(){//设置
          
      },
      button2(){//图像
      },
      button3(){//恢复上一次按等号后的式子
          this.input=localStorage.getItem('f');
      },
      button4(){//重启
          this.input='';
          this.output='';
          this.lastone='';
      },
      button5(){
          this.input+='7';
          this.lastone='7';
      },
      button6(){
          this.input+='8';
          this.lastone='8';
      },
      button7(){
          this.input+='9';
          this.lastone='9';
      },
      button8(){//撤销
          if(this.input!=''){
              this.input=this.input.substr(0,this.input.length-1);
          }
      },
      button9(){//清空输入框
          this.input='';
      },
      button10(){
          this.input+='4';
          this.lastone='4';
      },
      button11(){
          this.input+='5';
          this.lastone='5';
      },
      button12(){
          this.input+='6';
          this.lastone='6';
      },
      button13(){
          this.input+='*';
          this.lastone='*';
          localStorage.setItem('f',this.input);
      },
      button14(){
          this.input+='÷';
          this.lastone='÷';
          localStorage.setItem('f',this.input);
      },
      button15(){
          this.input+='1';
          this.lastone='1';
      },
      button16(){
          this.input+='2';
          this.lastone='2';
      },
      button17(){
          this.input+='3';
          this.lastone='3';
      },
      button18(){
          this.input+='+';
          this.lastone='+';
          localStorage.setItem('f',this.input);
      },
      button19(){
          this.input+='-';
          this.lastone='-';
          localStorage.setItem('f',this.input);
      },
      button20(){
          this.input+='0';
          this.lastone='0';
      },
      button21(){
          this.input+='.';
          this.lastone='.';
      },
      button22(){
          this.input+='*10^';
      },
      button23(){//Ans
          this.input+=this.lastout;
          this.lastone=this.lastout;
      },
      button24(){//提交数据给后端(=)
	        var _this = this;
          localStorage.setItem('f',this.input);
		      axios.get('/calculate',{//前端传给后端
              params: {
                  input: _this.input,
              }
          })
          .then(function (response){//后端传给前端
              console.log(response);
			        if(response.data.success==true)
                  _this.output = response.data.result;
			        else
			            _this.output = response.data.message;
          })
          .catch(function (error) { // 请求失败处理
              console.log(error);
          });
          if(this.output.substr(0,5)!='Error'){
              this.lastout=this.output;
          }
      },
      button25(ch){
          this.input+=ch;
          this.lastone=ch;
          localStorage.setItem('f',this.input);
      },
  },
  components:{
      Summation,
      DefiniteIntegral,
      Derivation,
      SolveEquation,
      Matrix,
      pluralism,
      Unary_multiple_equation,
      Number_system,
      paint,
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  color:white;
}
.background{
    width:100%;  
    height:100%;  /**宽高100%是为了图片铺满屏幕 */
    z-index:-1;
    //opacity:0.9;
    position: fixed;
}

</style>



