import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import paint from '@/components/paint'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homoe',
      component: home
    },
    {
      path: '/paint',
      name: 'paint',
      component: paint
    },
  ]
})



